import React from 'react';
import Link from './Link';
import NotFoundImg from '../img/404.jpg';

const NotFound = () => {
  const backgroundStyles = {
    height: '100vh',
    width: '100%',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    color: '#fff',
    flexDirection: 'column',
    background: `url(${NotFoundImg}) #444`,
    backgroundSize: 'cover',
    backgroundPosition: 'center'
  };

  return (
    <div style={backgroundStyles}>
      <h1 style={{ fontSize: '24rem' }}>404!</h1>
      <h2 style={{ fontSize: '3rem', margin: '0 0 2.5rem' }}>Page not found</h2>
      <Link href="/" to="Go back to home" notFound />
    </div>
  );
};

export default NotFound;
