import React from 'react';
import Navbar from './Navbar';

const Layout = ({ children }) => {
  const styles = {
    margin: '0 auto',
    maxWidth: '1200px',
    minHeight: '100vh'
  };

  return (
    <div style={styles}>
      <Navbar />
      {children}
    </div>
  );
};

export default Layout;
