import React from 'react';
import { withRouter } from 'react-router-dom';

const Link = ({ href, location, history, to, notFound }) => {
  const styles = {
    textDecoration: 'none',
    display: 'inline-block',
    color: location.pathname === href ? '#fff' : '#d4d4d4',
    margin: '0 1rem',
    border: notFound ? '1px solid #fff' : 'none',
    padding: notFound ? '1rem 2rem' : '0',
    background: notFound ? 'rgba(0,0,0,.4)' : 'none'
  };

  const handleClick = e => {
    e.preventDefault();
    history.push(href);
  };

  return (
    <a href={href} onClick={handleClick} style={styles}>
      {to}
    </a>
  );
};

export default withRouter(Link);
